@import "../../css/shared.scss";

.p-home{
  .home-top{
    height: px(680);
  }

  .home-container{
    background: #0a23b7;
    border-radius: px(20);
    margin: 0 px(30);
    padding-bottom: px(30);
  }

  .btn-top{
    background: linear-gradient(#FBE777, #F7CB45);
    color: #773F19;
    font-size: px(40);
    height: px(72);
    line-height: px(72);
    text-align: center;
    width: px(310);
    margin: 0 auto 0 auto;
    position: relative;
    border-radius: px(36);
    top: px(-26);
    box-shadow: 0 0 px(30) rgba(0, 0, 0, 0.4);
  }

  .list{
    display: flex;
    justify-content: space-around;
    margin: 0;
    padding: 0;

    li{
      //display: inline-block;
      padding-top: px(100);
      width: px(100);
      background: red;
      text-align: center;
      font-size: px(28);
      color: #fff;
      background: url('../../images/ico_01.png') no-repeat;
      background-size: 100% auto;

      &:nth-of-type(2) {
        background-image: url('../../images/ico_02.png');
      }
      &:nth-of-type(3) {
        background-image: url('../../images/ico_03.png');
      }
    }
  }

  .card{
    padding: px(38) px(38);
    background: #fff;
    border-radius: px(18);
    margin: px(30) px(30) 0 px(30);
    color: #333;

    p{
      font-size: px(42);
      color: #0039D9;
      margin: 0;
    }

    i{
      display: block;
      font-style: normal;
      color: #666;
      margin-top: px(10);
    }
  }

  .btn{
    background: linear-gradient(#FBE777, #F7CB45);
    color: #773F19;
    font-size: px(40);
    height: px(72);
    line-height: px(72);
    text-align: center;
    width: px(560);
    margin: px(30) auto px(120) auto;
    border-radius: px(6);
    box-shadow: 0 0 px(30) rgba(0, 0, 0, 0.4);
  }

  .row{
    display: flex;
    justify-content: space-between;
    padding: px(54) px(38);
  }

  .utxo{
    font-size: px(40);
    font-weight: bold;
    padding-left: px(80);
    border-left: 1px solid #ddd;
  }

  .back {
    position: fixed;
    left: px(30);
    top: px(30);
    width: px(50);
    height: px(50);
    background: red;

  }
}
